body{
    margin: 0;
    background-color: rgba(238, 23, 209, 0.577);
}
input{
    width: 30rem;
    height: 3rem;
    border-radius: 5px;
    background-color: antiquewhite;
    text-align: center;
}
button{
    width: 7rem;
    margin: 10px;
    height: 2.8rem;
    border-radius: 10px;
    background-color: rgb(197, 28, 223);
    box-shadow: 10px 10px 60px 3px inset rgba(0, 0, 0, 0.341);
}
button:hover{
    background-color: rgb(251, 121, 240);
}
form{
    display: flex;
    flex-wrap: wrap;
    place-content: center;
    margin:10px;
}
.conteiner-general{
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    place-content: center;
}
.conteiner-form{
    display: flex;
    flex-wrap: wrap;
     place-content: center;
}

img{
    width: 90%;
    height: 18rem;
    border-radius: 10px;
    
}
.counter-picture{
    display: grid;
    place-items: center;
    border-radius: 20px;
    border-color: rgb(234, 7, 113);
    margin: 5px;
    width: 18rem;
    height: 28rem;
    background-color: rgb(197, 28, 223);
    text-align: center;
    box-shadow: 10px 10px 60px 3px inset rgba(0, 0, 0, 0.341);
}

h1{
    color: rgba(138, 10, 121, 0.835);
}


